/* 메뉴 관련 Css */

  /* 메뉴 배경 */
  .ReactNavContainer
  {
    background-color: hsl(221, 18%, 31%);
  }
  
    /* 메뉴 최소 크기 및 좌우 라인 */
  .ReactNavContainer .nav .nav-item
  {
    border-right-width: 1px;
    border-right-style: solid;
    border-right-color: #cccccc;
  
    border-left-width: 1px;
    border-left-style: solid;
    border-left-color: #cccccc;
  
    min-width: 7rem;
    
  }
  
    /* 메뉴 글씨 색, 글씨체, 크기 */
  .ReactNavContainer .nav .nav-item a
  {
    
    color: white;
    font-size: 1.5rem;
    font-family: 'Dongle', sans-serif;
  }
  
    /* 메뉴 숨기기 */
  .hidden
  {
    display: none !important;
  }
  
  
  .ReactMainContainer
  {
    display: flex;
  }
  
  
  /* 각 컴포넌트 메뉴 최소 크기 지정 */
  .ReactMainContainer .ReactMainMenu .HeaderMenu
  {
    min-width: 15rem;
    max-width: 23rem;
    
    border: #282c34;
    border-style: solid;
    border-width: 1px;
  
    height: 100%;
  }
  
  .ReactMainContainer .ReactMainMenu .MainMenu
  {
    min-width: 15rem;
    max-width: 23rem;
  
    border: #282c34;
    border-style: solid;
    border-width: 1px;
  
    height: 100%;
  }
  
  .ReactMainContainer .ReactMainMenu .FooterMenu
  {
    min-width: 15rem;
    max-width: 23rem;
  
    border: #282c34;
    border-style: solid;
    border-width: 1px;
  
    height: 100%;
  }
  
    /* 추가 컴포넌트 */
  .ReactMainContainer .AddElement
  {
    display: flex;
    align-items: center;
    justify-items: center;
  
    border: 1px ridge #4789e7;
    height: 2px;
    width: 100vw;
  
    margin-top: 11px;
    
  }
  
    /* 추가 컴포넌트 */
  .ReactMainContainer .AddElement .Line
  {
  list-style: none;
  
    background: #4789e7;
  
    height: 25px;
    width: 150px;
  
    margin: 0 auto;
  
    border-radius: 8px;
    border: black;
    border-style: solid;
    border-width: 1px;;
  
    
  }
  
  
  /* 헤더 메뉴 컴포넌트 디스플레이 설정*/
  .ReactMainContainer .ReactMainMenu .Componet .HeaderText .TextComponet
  {
    display: grid;
    grid-template-columns: 1fr 1fr;
  
    
  }
  
  /* 헤더 메뉴 컴포넌트 제목 설정 */
  .ReactMainContainer .ReactMainMenu .Componet .HeaderText .TextComponet span
  {
    width: 5rem;
    margin: 0 auto;
  
    border-right: #282c34;
    border-right-style: solid;
    border-right-width: 1px;
  
    border-bottom: #282c34;
    border-bottom-style: solid;
    border-bottom-width: 1px;
  
  }
  
  /* 헤더 메뉴 컴포넌트 이미지 설정 */
  .ReactMainContainer .ReactMainMenu .Componet .HeaderText .TextComponet img
  {
    width: 15rem;
  
    border-bottom: #282c34;
    border-bottom-style: solid;
    border-bottom-width: 1px;
  }